






































.fab-container {
	position: relative;
	display: flex;
	align-items: center;
	justify-content: center;
	min-width: 56px;
	height: 56px;
	padding: 16px;
	cursor: pointer;
	border-radius: 5em;
	box-shadow: 0 2px 5px rgba(0, 0, 0, 0.5);

	.fab-icon {
		user-select: none;
		&--close {
			transform: rotate(-45deg);
		}
		&.spin-enter-active,
		&.spin-leave-active {
			transition: opacity 0.2s, transform 0.2s;
		}
		&.spin-enter,
		&.spin-leave-to {
			opacity: 0;
			transform-origin: center;
		}
		&.spin-enter {
			transform: rotate(-90deg);
		}
		&.spin-leave-to {
			transform: rotate(90deg);
		}
	}
}
