




































nav.sidebar {
	z-index: 2;
	background: var(--darkGrey);
	box-shadow: 2px 3px 8px 0 rgba(199, 199, 199, 1);
}
.sidebar-list {
	padding: 0;
	list-style-type: none;
}
.sidebar-entry {
	.link {
		display: flex;
		align-items: center;
		padding: 8px;
		color: white;
		text-decoration: none;

		i {
			margin-right: 8px;
		}

		&.router-link-active {
			color: white;
			background-image: linear-gradient(
				to right,
				var(--primaryColor),
				var(--accentColor)
			);
		}
	}

	&:hover {
		background: #5c5c5c;
	}
}
