



































































































#app {
	display: flex;
	flex-direction: column;
	min-height: 100vh;
	padding: 0;
}

$header-height: 56px;

main {
	flex: 1;
	// display: contents;
	max-width: 100% !important;
	padding-top: $header-height;
	padding-right: 25px;
	padding-left: 25px;
	&.with-sidebar {
		padding-left: 200px;
	}
}

.header {
	position: fixed;
	top: 0;
	height: $header-height;
	background: white;
	box-shadow: 0 2px 8px 0 rgb(140, 139, 139);
}
.sidebar {
	position: fixed;
	top: $header-height;
	bottom: 0;
	width: 175px;
}
.footer {
	padding-left: 175px;
}
