





































































































.fab-position {
	position: fixed;
	right: 5rem;
	bottom: 2.5rem;
	z-index: 10;
}
.fab-main {
	z-index: 2;
}
.actions-container {
	position: absolute;
	bottom: 100%;
	left: 50%;
	transform: translateX(-50%);
}

.staggered-transition {
	opacity: 1;
	transition: all 0.5s ease;
}
.staggered-enter,
.staggered-leave {
	opacity: 0;
}

